import axios, { Axios, CreateAxiosDefaults } from 'axios'
import { BNify } from '../../core'
import { IntegrationClientModel } from '../integration-client.model'

export class EthenaClient implements IntegrationClientModel {
  // Axios instance
  private axios: Axios

  constructor() {
    this.axios = this.initAxios()
  }

  /**
   * Axios initialization
   */
  private initAxios() {
    const options: CreateAxiosDefaults = {
      baseURL: 'https://app.ethena.fi/api/',
      headers: {
        'content-type': 'application/json',
      },
    }
    return axios.create(options)
  }

  // Methods

  public async getApr(tokenSymbol: string): Promise<number> {
    switch (tokenSymbol) {
      case 'USDe':
        return this.getUSDeApr()
      default:
        return 0
    }
  }

  public async getUSDeApr(): Promise<number> {
    return this.axios
      .request({
        url: 'yields/protocol-and-staking-yield',
        method: 'GET',
        responseType: 'json',
      })
      .then((response) => BNify(response?.data.stakingYield.value).toNumber())
      .catch(() => 0)
  }
}
